<script setup lang="ts">
import { ref, onMounted } from 'vue'
import BookingSummary from '@/components/BookingSummary.vue'
import CarResult from '@/components/CarResult.vue'
import { Car as CarData } from '@/types'
// @ts-ignore
import $ from 'jquery'
import CarService from '@/services/car-service'
import { getSessionPrefix } from '@/utils/common'
import router from '@/router'

const props = defineProps(['currentStep'])
const { retrieveUserSelectedCar } = CarService()
const car : CarData = retrieveUserSelectedCar()

const toggleExtraOptions = () => {
   $('.extra-options').slideToggle()
}

onMounted(() => {
   
   checkUserSessions()
   //removeUserSessions()
})

const checkUserSessions = () => {

   const prefix = getSessionPrefix()
   const search = localStorage.getItem(`${prefix}_user_search`)
   const car = localStorage.getItem(`${prefix}_selected_car`)
   const booking = localStorage.getItem(`${prefix}_booking_number`)

   if (!search || !car || !booking) {
      router.push({name: 'Home'})
   }
}

const removeUserSessions = () => {

   const prefix = getSessionPrefix()
   localStorage.removeItem(`${prefix}_user_search`)
   localStorage.removeItem(`${prefix}_selected_car`)
   localStorage.removeItem(`${prefix}_booking_number`)
}

</script>