      //    }
      // } else {
      //    console.log(tokenResponse)
      // }
      
      const response = await finalizeeBooking(bookingNumber as string, contactDetails.value, paymentDetails.value)
      isLoading.value = false
      
      if (response.success) {
         router.push({name: 'BookingWizard', params: {step: 'finish'}})
      } else {
         const error = handleApiError(response)
         errors.value.responseError = error
      }
   }
}

const validateForm = () => {
   
   let response = true
   
   if (contactDetails.value.firstName === '') {
      errors.value.firstName = true
      response = false
   }
   
   if (contactDetails.value.lastName === '') {
      errors.value.lastName = true
      response = false
   }

   if (contactDetails.value.phoneNumber === '') {
      errors.value.phoneNumber = 'Required'
      response = false
   }else if (contactDetails.value.phoneNumber.length < 11) {
      errors.value.phoneNumber = 'Invalid'
      response = false
   }

   if (contactDetails.value.email === '') {
      errors.value.email = 'Required'
      response = false
   } else if (!isValidEmail(contactDetails.value.email)) {
      errors.value.email = 'Invalid'
      response = false
   }
   
   if (contactDetails.value.needConfirmation === false) {
      
      if (paymentDetails.value.card.name === '') {
         errors.value.cardName = true
         response = false
      }
   
      if (paymentDetails.value.card.number === '') {
         errors.value.cardNumber = 'Required'
         response = false
      } else if (!isValidCardNumber(paymentDetails.value.card.number)) {
         errors.value.cardNumber = 'Invalid'
         response = false
      }
   
      if (paymentDetails.value.card.cvc === '') {
         errors.value.cardCVC = true
         response = false
      }
   
      if (paymentDetails.value.card.expiration === '') {
         errors.value.cardExpiration = true
         response = false
      }
   }

   return response
}

const handlePhoneChange = (value: string) => {
   contactDetails.value.phoneNumber = value
}

const getPaymentToken = async () : Promise<any> => {

   let paymentToken

   /* Todo : generate a valid payment token from payment gateway */
   paymentToken = '123456789abcef'

   return { success: true, token: paymentToken }
}

const getCardBrand = (cardNumber: string) => {
   const cardType = creditCardType(cardNumber);

   if (cardType.length > 0) {
      return cardType[0].niceType
   }

   return 'Unknown'
}

onMounted(async () => {

   const response: any = retrieveUserSelectedCar()
   if (response && response.price === null) {
      contactDetails.value.needConfirmation = true
   }

   const sessionResponse = await checkAndCreateSession()
   if (!sessionResponse.status) {
      notif.value = { show: true, type: 'error', message: 'Error', messageDetails: 'Payment cannot be processed at the moment'}
   } else {
      paymentDetails.value.session = sessionResponse.session
   }
})

onBeforeMount(() => {
   const script = document.createElement('script')
   script.src = credimax.sdk
   script.dataset.error = 'errorCallback'
   script.dataset.cancel = 'cancelCallback'
   script.onload = async () => {
      const response = await configure3DS()
      if (!response) {
         notif.value = { show: true, type: 'error', message: 'Error', messageDetails: 'Payment partner configuration error'}
      }
   }
   document.head.appendChild(script)
})

const configure3DS = (): Promise<boolean> => {
   return new Promise((resolve, reject) => {
      window.ThreeDS.configure({
         merchantId: credimax.merchant_id,
         sessionId: paymentDetails.value.session,
         containerId: '3DSUI',
         callback: function () {
            resolve(window.ThreeDS.isConfigured())
         },
         configuration: {
            userLanguage: 'en-AU',
            wsVersion: 77,
         },
      })
   })
}

const initiate3DSAuthentication = async (order: string, transaction: string) => {

   if (!window.ThreeDS.isConfigured()) {
      await configure3DS()
   }

   if (window.ThreeDS.isConfigured()) {

      window.ThreeDS.initiateAuthentication(order, transaction, function (data: any) {
         if (data && data.error) {
               var error = data.error
               notif.value = { show: true, type: 'error', message: 'Error: ' + error.code, messageDetails: error.msg}
         } else {
            if (data.gatewayRecommendation == 'PROCEED') {
               authenticatePayer(order, transaction)
            } else if (data.gatewayRecommendation == 'RESUBMIT_WITH_ALTERNATIVE_PAYMENT_DETAILS') {
               notif.value = { show: true, type: 'error', message: 'Error: ' + error.code, messageDetails: 'The payment requires 3DS while your card does not support 3DS. Please use a different card'}
            } else {
               notif.value = { show: true, type: 'error', message: 'Error', messageDetails: 'Unknown error from payment partner'}
            }
         }
      }, {})
   } else {
      notif.value = { show: true, type: 'error', message: 'Error', messageDetails: 'Payment partner configuration error'}
   }
}

const authenticatePayer = (order: string, transaction: string) => {

   var optionalParams = {
      fullScreenRedirect: true
   }
   
   window.ThreeDS.authenticatePayer(order, transaction, function (data: any) {
      if (!data.error) {
         if (data.gatewayRecommendation == 'PROCEED') {
            console.log(data)
         }
      }
   }, optionalParams)
}

const checkAndCreateSession = async () : Promise<{status: boolean, session: string | null}> => {

   const sessionResponse = await createSession()
   if (sessionResponse.status == 200) {
      savePaymentSession(sessionResponse.data.session)
      return {status: true, session: sessionResponse.data.session}
   } else {
      return {status: false, session: null}
   }
}

const notifClosed = () : void => {
   notif.value.show = false
}


